@import "static-classes.scss";
@import "./navigation.scss";
@import "./dialogs.scss";
@import "messages.scss";
@import "mes-kebab.scss";
@import "mes-modal.scss";

@mixin max-screen($res) {
    @media screen and (max-width: $res) {
        @content;
    }
}

.chat {
    .container-grid {
        display: grid;
        grid-template-columns: 192px 384px 1fr;
        // grid-template-columns: 1fr 2fr 7fr;
        @include max-screen(1250px) {
            grid-template-columns: 192px 284px 1fr;
        }
    }

    &__navigation {
        // .chat__navigation
        height: 100vh;
        padding: 10vh 0 10vh 0;
        background: $color-violet;
        grid-column: 1 / 2;
    }

    &__dialogs {
        // .chat__dialogs
        height: 100vh;
        grid-column: 2 / 3;
        display: flex;
        flex-direction: column;
        border-right: 1px solid $color-border-light-gray;
    }

    &__messages {
        // .chat__messages
        height: 100vh;
        grid-column: 3 / 4;
        display: flex;
        flex-direction: column;
    }
}
